import React, { useEffect } from 'react';
import { Form, Col, Row } from 'antd';
import Btn from 'components/common/Btn/Btn';
import YearSemesterInputV2 from '../../../components/Eem/YearSemesterInput_v2';
import { SelectAjax } from '../../../components/common/index';
import { getTrainTypes } from '../../../services/trainType';
import { requiredRule } from '../../../utils/validateRules';
import { getQueryParams, updateQueryParams } from '../../../utils/urlUtil';

const FormSearch = ({ form, onSubmit }) => {
  const { validateFields, getFieldDecorator } = form;

  useEffect(() => {
    form.setFieldsValue(getQueryParams());
  }, []);
  
  const onSearch = async e => {
    e.preventDefault();
    validateFields(async (err, values) => {
      if (err) return;
      if (onSubmit) {
        await onSubmit(values);
      }
      updateQueryParams(values);
    });
  };
  return (
    <Form colon={false}>
      <Row gutter={[12, 12]}>
        <Col span={6}>
          <Form.Item label="Hệ">
            {getFieldDecorator('trainTypeId', {
              rules: [requiredRule],
            })(<SelectAjax service={getTrainTypes} />)}
          </Form.Item>
        </Col>
        <YearSemesterInputV2 form={form} />
        <Btn style={{ marginTop: 48 }} primary onClick={onSearch}>
          Tìm kiếm
        </Btn>
      </Row>
    </Form>
  );
};

export default Form.create()(FormSearch);



