import { request } from 'utils';

const prefix = '/api/education';

export const getRegisterSessions = ({
  trainTypeId,
  yearEduId,
  semesterId,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/RegisterSession`,
    method: 'GET',
    params: { trainTypeId, yearEduId, semesterId, page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const updateGradeEndDate = ({ id, gradeEndDate } = {}) =>
  request({
    prefix,
    url: `/RegisterSession/UpdateGradeEndDate`,
    method: 'POST',
    params: {},
    data: { id, gradeEndDate },
  });



