import React, { useState, useEffect } from 'react';
import { Form, message } from 'antd';
import Card from '../../../components/common/Card/index';
import CardTitle from '../../../components/common/CardTitle/index';
import TableDataEditRow from '../../../components/common/TableDataEditRow';
import { getRegisterSessions, updateGradeEndDate } from '../../../services/registerSession';
import { dateTimeToString } from '../../../utils/formatUtil';
import { getQueryParams } from '../../../utils/urlUtil';
import DateTimePickerString from '../../../components/common/Input/DateTimePickerString';
import FormSearch from './FormSearch';
// import Table from './Table';

const FORMAT_TIME = 'HH:mm DD/MM/YYYY';

export default props => {
  const [paramSearch, setParamSearch] = useState({});

  useEffect(() => {
    setParamSearch(getQueryParams());
  }, [props]);

  const columns = [
    {
      title: 'Hệ',
      key: 'trainTypeLabel',
      dataIndex: 'trainTypeLabel',
    },
    {
      title: 'Năm học',
      key: 'yearEduLabel',
      dataIndex: 'yearEduLabel',
    },
    {
      title: 'Học kì',
      key: 'semesterLabel',
      dataIndex: 'semesterLabel',
    },
    {
      title: 'Từ ngày',
      key: 'startDate',
      dataIndex: 'startDate',
      render: text => {
        return dateTimeToString(text, FORMAT_TIME);
      },
    },
    {
      title: 'Tới ngày',
      key: 'endDate',
      dataIndex: 'endDate',
      render: text => {
        return dateTimeToString(text, FORMAT_TIME);
      },
    },
    {
      title: 'Thời hạn chấm điểm',
      key: 'gradeEndDate',
      dataIndex: 'gradeEndDate',
      render: text => {
        return dateTimeToString(text, FORMAT_TIME);
      },
      editable: true,
      renderFormItemEdit: ({ form, record }) => (
        <Form.Item>
          {form.getFieldDecorator('gradeEndDate', {
            initialValue: record.gradeEndDate,
          })(<DateTimePickerString />)}
        </Form.Item>
      ),
    },
  ];
  const handleSaveRowData = async rowData => {
    try {
      await updateGradeEndDate(rowData);
      message.success("Cập nhật thành công")
      return rowData;
    } catch (e) {
      return null;
    }
  };
  return (
    <Card>
      <CardTitle title="Thời hạn chấm điểm" />
      <FormSearch />
      <TableDataEditRow
        paramSearch={paramSearch}
        service={getRegisterSessions}
        columns={columns}
        handleSaveRowData={handleSaveRowData}
        allowGetData={paramSearch.trainTypeId}
        rowKey="id"
      />
    </Card>
  );
};



